
.drap-before,.drap-after {
  width: 300px;
  height: 150px;
  border: 1px solid;
  box-sizing: border-box;
  text-align: center;
  margin: 0 auto;
}
.drap-after {
  margin-top: 100px;
}
.seePos {
  margin: 0 auto;
  width: 300px;
  height: 300px;
  display: table;
  text-align: center;
}
.seePos > p {
  display: table-cell;
  vertical-align: middle;
}



